<template>
  <div v-loading='loading'>
    <cms-breadcrumb to="/selfShops/list"  @click="rest">
      <li>{{$route.name}}</li>
    </cms-breadcrumb>
    <div class="cms-form-layout">
      <cms-prompt collapse='true'>
        <li>带 * 为必填项</li>
        <li>编辑保存只对商家信息进行修改</li>
        <li>如需修改对应的会员信息，请移至商城->会员->商城会员下进行编辑操作</li>
      </cms-prompt>
      <el-form ref="form" :rules="saveRules" :model="formData" label-width="210px" v-if="!loading" v-loading="loading">
        <el-form-item label="店铺名称" prop="storeName" :error='errorMessage.storeName' >
          <el-input v-model="formData.storeName" :autofocus="true" ref="first"></el-input>
        </el-form-item>
        <el-form-item label="店家账号" prop="member.username" v-if="formData.member">
          <el-input v-model="formData.member.username" disabled></el-input>
          <div class='gray'>用于登录商城会员中心</div>
        </el-form-item>
        <el-form-item label="商家账号" prop="storeUsername" v-if="formData.member">
          <el-input v-model="formData.storeUsername" disabled></el-input>
          <div class='gray'>用于登录商家中心</div>
        </el-form-item>
        <el-form-item label="姓名" prop="contactName" :error='errorMessage.contactName'>
          <el-input v-model="formData.contactName"></el-input>
        </el-form-item>
        <el-form-item label="手机号" prop="contactMobile" :error='errorMessage.contactMobile'>
          <el-input v-model="formData.contactMobile" maxlength="11"></el-input>
          <div class='gray'>用于登录商家中心</div>
        </el-form-item>
        <el-form-item label="邮箱" prop="contactMail" :error='errorMessage.contactMail'>
          <el-input v-model="formData.contactMail" maxlength="50"></el-input>
          <div class='gray'>用于登录商家中心</div>
        </el-form-item>
        <el-form-item label="登录密码" prop="pStr" :error='errorMessage.pStr'>
          <el-input v-model="formData.pStr" type="password"></el-input>
          <div class='gray'>6-16位数字、字母组合，留空不修改</div>
        </el-form-item>
        <el-form-item label="确认密码" prop="checkPassword" :error='errorMessage.checkPassword'>
          <el-input v-model="formData.checkPassword" type="password"></el-input>
        </el-form-item>
      </el-form>
      <div class="cms-form-footer">
        <cms-button @click="update"  v-color >保存</cms-button>
        <cms-button type='rest' @click="reset">重置</cms-button>
      </div>
    </div>
  </div>
</template>
<script>
import rules from '@/rules'
import form from '@/mixins/form.js'
import { selfShopsUpdate, selfShopsGet, selfVaildName } from '@/api/shop/selfShops.js'
import {
  validName,
  validUsername,
  validPhone,
  validContactMail
} from '@/api/shop/shopManage.js'
var defaultData = {
  'id': '',
  'contactMail': '',
  'contactMobile': '',
  'contactName': '',
  'enabled': '',
  'storeName': '',
  'pStr': '',
  'checkPassword': ''
}
export default {
  mixins: [form],
  data() {
    var ischeckName = (rule, value, callback) => { // 验证姓名
      selfVaildName({
        id: this.formData.id,
        validContactName: this.formData.contactName
      }).then(res => {
        if (res.data) {
          callback()
        } else {
          callback(new Error('姓名已存在，不允许重复添加'))
        }
      })
    }
    var validUsernam = (rule, value, callback) => {
      // 验证商家账号唯一
      validUsername({
        validUsername: this.formData.storeUsername
      }).then(res => {
        if (res.data) {
          callback()
        } else {
          callback(new Error('该商家账号已存在，不允许重复添加'))
        }
      })
    }
    var validatePass = (rule, value, callback) => { // 验证 密码
      const patrnPass = /^[a-zA-Z\d]+$/
      const patrnCh = /^[u4e00-u9fa5]*$/
      if (value === '') {
        callback()
      } else if (!patrnPass.test(value) && !patrnCh.test(value)) {
        callback(new Error('只能输入数字和大小写字母'))
      } else {
        callback()
      }
    }
    var validatePass2 = (rule, value, callback) => {
      if (this.formData.pStr !== '' && value === '') {
        callback(new Error('请再次输入密码'))
      } else if (value !== this.formData.pStr) {
        callback(new Error('两次输入密码不一致!'))
      } else {
        callback()
      }
    }
    var validContactMaill = (rule, value, callback) => { // 验证联系人邮箱唯一
      validContactMail({
        validContactMail: this.formData.contactMail,
        id: this.formData.id
      }).then(res => {
        if (res.data) {
          callback()
        } else {
          callback(new Error('联系人邮箱已存在，不允许重复添加'))
        }
      })
    }
    var validUserPhone = (rule, value, callback) => { // 验证手机号唯一
      validPhone({
        validPhone: this.formData.contactMobile,
        id: this.formData.id
      }).then(res => {
        if (res.data) {
          callback()
        } else {
          callback(new Error('手机号已注册店铺，不允许重复添加'))
        }
      })
    }
    var validStoreName = (rule, value, callback) => {
      // 验证店铺名称唯一
      validName({
        validName: this.formData.storeName,
        id: this.formData.id
      }).then(res => {
        if (res.data) {
          callback()
        } else {
          callback(new Error('店铺名已存在，不允许重复添加'))
        }
      })
    }
    return {
      formData: Object.assign({}, defaultData),
      saveRules: {// 新增规则
        storeName: [rules.required('请填写店铺名称'), rules.len(1, 150), rules.custom(validStoreName)],
        colorValue: [rules.required('请填写商家账号'), rules.len(1, 150), rules.custom(validUsernam)],
        contactName: [rules.required('请填写姓名'), rules.len(1, 150), rules.custom(ischeckName)],
        contactMobile: [rules.required('请填写手机号'), rules.len(1, 150), rules.phone(), rules.custom(validUserPhone)],
        contactMail: [rules.required('请填写邮箱'), rules.len(1, 150), rules.custom(validContactMaill)],
        pStr: [rules.custom(validatePass), rules.len(6, 16)],
        checkPassword: [rules.custom(validatePass2)]
      }
    }
  },
  methods: {
    getFormData(id) { // 通过id获取表单信息
      selfShopsGet({ id: id }).then(res => {
        this.formData = Object.assign(defaultData, res.data)
        this.formData.pStr = ''// 强制置空。预防万一
        this.formData.checkPassword = '' // 强制置空。预防万一
      })
    },
    update() {
      const params = {
        'id': this.formData.id,
        'contactMail': this.formData.contactMail,
        'contactMobile': this.formData.contactMobile,
        'contactName': this.formData.contactName,
        'enabled': this.formData.enabled,
        'storeName': this.formData.storeName,
        'pStr': this.formData.pStr ? this.$cms.encrypt(this.formData.pStr) : ''
      }
      this.updateForm(selfShopsUpdate, params, '/selfShops/list', 'list')
    },
    reset() {
      this.getFormData(this.$route.query.id)
      this.autoFocus()
      this.$refs.form.resetFields()
    }
  },
  created() {
    this.getFormData(this.$route.query.id)
  }
}
</script>




